<template lang="html">

  <div class="root_content">
    <ul>
      <li class="read_card" v-for="read in readList">
        <a class="card_item" target="_blank" :href="read.newsArray[0].url">
          <div class="read_title">{{ read.title}}</div>
          <div v-if="read.summary" class="read_desc">
            {{ read.summary}}
          </div>
        </a>
      </li>
    </ul>
    <div v-if="readList.length != 0">
      <button class="button_load_more" @click="loadMore">更多资讯</button>
    </div>
  </div>

</template>

<script>
export default {
  data () {
    return {
      apiUrl: 'https://api.readhub.me/topic?pageSize=20',
      readList: []
    }
  },
  created() {
    this.getReadList()
    document.title = '科技资讯 - 一个就够了'
  },
  computed: {},
  mounted () {},
  methods: {
    getReadList(url, needContact) {
      this.$http.options.emulateJSON = true;
      if (!url) {
        url = this.apiUrl
        console.log('url : ' + this.apiUrl);
      }
      this.$http.get(this.$Api(url))
          .then((response) => {
            emulateJSON: true
            console.log(response.data.data);
            if (needContact) {
              this.readList = this.uniquel(this.readList.concat(response.data.data))
            } else {
              this.readList = response.data.data
            }
          })
          .catch(function(response) {
              console.log(response)
          })
    },
    loadMore() {
      var lastReadId = this.readList[this.readList.length-1].id;
      var requestUrl = 'https://api.readhub.me/topic?lastCursor=' + lastReadId + '&pageSize=20';
      this.getReadList(requestUrl, true)
    },
    uniquel(array) {
      var flags = [], output = [], l = array.length, i;
      for(i=0; i<l; i++) {
          if(flags[array[i].id]) continue;
          flags[array[i].id] = true;
          output.push(array[i]);
      }
      return output;
    }
  },
  components: {}
}
</script>

<style lang="css">
  .read_desc {
    margin-top: 10px;
    color: #565656;
    padding-left: 16px;
    padding-right: 16px;
  }
  .read_title {
    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.26em;
    padding-left: 26px;
    padding-right: 26px;
  }
  .card_item {
    color: #333;
    padding: 20px;
  }
  .read_card {
    width: 100%;
    margin-right: 10px;
    background: #ffffff;
    border-radius: 0px;
    margin-bottom: 38px;
    box-shadow: 0 0 .455rem .08rem rgba(0,0,0,.05);
  }
  .button_load_more {
    margin-top: 0px;
    margin-bottom: 38px;
  }
</style>



